import React, { Component } from 'react';
import { H1, H2, H5, P, Subtitle, Transition, NextBtn, Highlights } from '../css/fonts.js';
import { OuterContainer, InnerContainer, Br, TwoCol, TwoColSpec } from '../css/container.js';
import { shContent } from '../content.js';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import Image from '../components/Image';
import Tools from '../components/Tools';

class SmartHygiene extends Component {
  render() {
    return (
      <div>
        <Navbar/>
        <InnerContainer>
          <br/><br/><br/><br/>
          <Br/><Br/>
          <H1 large center>{shContent.title}</H1>
          <img src={shContent.header} alt={shContent.header} style={{"width":"100%"}}/>
          <br/><br/><Br/><Br/><Br/>

          <TwoCol>
            <P right dangerouslySetInnerHTML={{ __html: shContent.other }}/>
            <H5 dangerouslySetInnerHTML={{ __html: shContent.role }}/>
          </TwoCol>
          <br/><br/>
        </InnerContainer>
       <OuterContainer>
          <InnerContainer>
            <div>
            <Subtitle>CONTEXT</Subtitle>
            <H5 dangerouslySetInnerHTML={{ __html: shContent.context }}/>
            </div>
            <br/><br/>
            <div>
            <Subtitle>RESEARCHING THE DESIGN SPACE</Subtitle>
            <H5 dangerouslySetInnerHTML={{ __html: shContent.sd1 }}/>
            <img src={shContent.sdimg} alt={shContent.sdimg} style={{"width":"80%"}}/>
            <br/><br/><br/>
            <H5 dangerouslySetInnerHTML={{ __html: shContent.deskresearch }}/>
            <br/>
            <H5 dangerouslySetInnerHTML={{ __html: shContent.contextual_inquiry }} />
            <TwoColSpec>
              <img src={shContent.ciplanimg} alt={shContent.ciplanimg} style={{"width":"100%"}}/>
              <img src={shContent.cidataimg} alt={shContent.cidataimg} style={{"width":"100%"}}/>
            </TwoColSpec>
            <br/><br/>

            </div>
              <br/><br/>
              <Subtitle>THE PROBLEM</Subtitle>
              <H5 dangerouslySetInnerHTML={{ __html: shContent.poi }} />
              <Highlights dangerouslySetInnerHTML={{ __html: shContent.argument }} />
              <br/><br/>
              <Subtitle>OUR SOLUTION</Subtitle>
              <H5 dangerouslySetInnerHTML={{ __html: shContent.pocp }} />
              <img src={shContent.pocpimg1} alt={shContent.pocpimg1} style={{"width":"100%"}}/>
              <br/><br/><br/><br/>
              <Subtitle>USABILITY TESTING</Subtitle>
              <br/><br/>
              <img src={shContent.utimg} alt={shContent.utimg} style={{"width":"100%"}}/>
              <br/><br/>

              <Subtitle>FINDINGS</Subtitle>
              <H5 dangerouslySetInnerHTML={{ __html: shContent.finding }} />
              <br/><br/>
              <Subtitle>LEARNINGS</Subtitle>
              <H5 dangerouslySetInnerHTML={{ __html: shContent.learnings }} />


          </InnerContainer>
        </OuterContainer>
        <NextBtn to={shContent.link}>
          <div dangerouslySetInnerHTML={{ __html: shContent.action }} />
        </NextBtn>
        <br/><br/><br/><br/>
        <Footer/>
      </div>
    );
  }
}

export default SmartHygiene;
