import styled from 'styled-components';
import { device } from './breakpoints.js';
import { Link } from 'react-router-dom';

//For titles on the header
export const H1 = styled.h1 `
    font-family: 'Karla', sans-serif;
    font-weight: ${props => props.bold ? "500":"300"};
    color: ${props => props.white ? "white":"#000"};
    text-align: ${props => props.center ? "center":""};
    @media ${device.mobileS} {
        font-size: 30px;
    }
    @media ${device.laptopL} {
        font-size: ${props => props.large ? "4.0em!important":"3.0em!important"};
        /* letter-spacing: 0.03em; */
    }
    @media ${device.desktop} {
        /* font-size: 4.0em!important; */
        font-size: ${props => props.large ? "5.0em!important":"4.0em!important"};
        /* letter-spacing: 0.03em; */
    }
`

//For questions on the homepage and subtitles on about page
export const H2 = styled.h2 `
    font-family: 'Karla', sans-serif;
    /* font-weight: ${props => props.bold ? "700":"400"}; */
    font-weight: 700;
    text-align: ${props => props.center ? "center":""};
    /* margin: auto; */
    padding-bottom: 10px;
    /* color: #FE4E60; */
    color: #333;
    @media ${device.mobileS} {
        font-size: 1em;
    }
    @media ${device.laptopL} {
        font-size: 1.75em!important;
    }
    @media ${device.desktop} {
        font-size: 3.5em!important;
    }
`

//For all paragraphs
export const H3 = styled.h3 `
    font-family: 'Karla', sans-serif;
    color: ${props => props.gray ? "#999999":"#333333"};
    line-height: 1.5em;
    font-weight: 400;
    @media ${device.mobileS} {
        font-size: 1em;
    }
    @media ${device.laptopL} {
        font-size: 1.75em;!important;
        line-height: 1.5em!important;
    }
    @media ${device.desktop} {
        font-size: 2em!important;
    }
`


//For card title in get involve page
export const H4 = styled.h4 `
    font-family: 'Karla', sans-serif;
    color: #000;
    line-height: 1.45em;
    font-weight: 400;
    padding-bottom: 8px;
    @media ${device.mobileS} {
        font-size: 1.25em;
    }
    @media ${device.laptopL} {
        font-size: 1.5em!important;

    }
    @media ${device.desktop} {
        font-size: 2.5em!important;
        /* letter-spacing: 0.05em; */
    }
`

//For all paragraphs
export const H5 = styled.h5 `
    font-family: 'Karla', sans-serif;
    color: ${props => props.light ? "#798897":"#333"};
    text-align: ${props => props.right ? "right":"left"};
    line-height: 1.6em;
    letter-spacing: 0.03em;
    padding-bottom: 10px;
    font-weight: ${props => props.bold ? "400":"300"};
    /* font-weight: 300; */
    @media ${device.mobileS} {
        font-size: 1em;
    }
    @media ${device.laptopL} {
        font-size: 1.20em;!important;
    }
    @media ${device.desktop} {
        font-size: 2em!important;
    }
`

export const H5U = styled(H5) `
    text-decoration: underline;
    text-underline-offset: 1px;
    text-decoration-color: #FFE1AD;
    text-decoration-thickness: 5px;
`

export const Highlights = styled(H5) `
    font-family: 'Karla', sans-serif;
    /* color: ${props => props.light ? "#798897":"#333"};     */
    color: #333;
    border: 1px solid #798897;
    width: 80%;
    margin: 32px auto 32px auto;
    line-height: 1.6em;
    letter-spacing: 0.03em;
    padding-bottom: 10px;
    text-align: ${props => props.left ? "":"center"};
    font-style: italic;
    font-weight: 300;
    /* font-weight: 300; */
    @media ${device.mobileS} {
        font-size: 1em;
        padding: 0.3em;!important;
    }
    @media ${device.laptopL} {
        font-size: 1.20em;!important;
        padding: 0.5em;!important;
    }
    @media ${device.desktop} {
        font-size: 2em!important;
        padding: 0.9em!important;
    }
`


//For footer
export const H6 = styled.h6 `
    font-family: 'Karla', sans-serif;
    color: #798897;
    font-weight: ${props => props.light ? "300":"700"};
    text-align: ${props => props.center ? "center":""};
    line-height: 1em;
    @media ${device.mobileS} {
      font-size: 0.8em;
    }
    @media ${device.laptopL} {
      font-size: 1.1em;!important;
      line-height: 1.3em!important;
    }
    @media ${device.desktop} {
      font-size: 1.9em!important;
    }
`

export const P = styled.p `
    font-family: 'Karla', sans-serif;
    color: #798897;
    text-align: right;
    line-height: 1.2em;
    letter-spacing: 0.03em;
    padding-bottom: 10px;
    font-weight: ${props => props.bold ? "400":"300"};
    /* font-weight: 300; */
    @media ${device.mobileS} {
        font-size: 1em;
    }
    @media ${device.laptopL} {
        font-size: 1em;!important;
    }
    @media ${device.desktop} {
        font-size: 1.5em!important;
    }
`

export const Subtitle = styled.h5 `
    font-family: 'Karla', sans-serif;
    color: #FFD400;
    letter-spacing: 0.03em;
    font-weight: 800;
    /* font-weight: 300; */
    @media ${device.mobileS} {
        font-size: 1.25em;
    }
    @media ${device.laptopL} {
        font-size: 1.5em;!important;
    }
    @media ${device.desktop} {
        font-size: 2.5em!important;
    }
`


export const Transition = styled.h5 `
    font-family: 'Karla', sans-serif;
    color: #798897;
    letter-spacing: 0.03em;
    font-weight: 300;
    text-align: center;
    transform: rotate(90deg);
  /* Safari */
  -webkit-transform: rotate(90deg);
  /* Firefox */
  -moz-transform: rotate(90deg);
  /* IE */
  -ms-transform: rotate(90deg);
  /* Opera */
  -o-transform: rotate(90deg);
    /* font-weight: 300; */
    @media ${device.mobileS} {
        font-size: 1em;
    }
    @media ${device.laptopL} {
        font-size: 1.20em;!important;
    }
    @media ${device.desktop} {
        font-size: 2em!important;
    }
`


export const Span = styled.span `
    color: ${props => props.subtitle ? "#333":"#FE4E60"};
    font-size: ${props => props.subtitle ? "0.75em":""};
    font-weight: ${props => props.subtitle ? "400":"700"};
`

export const A = styled.a `
    color: #90C4E9;
`

export const Large = styled.p `
    font-family: 'Karla', sans-serif;
    text-align: center;
    color: rgba(177, 190, 234, 0.4);
    font-size: 120px!important;
    font-weight: 900;
    letter-spacing: 10px;
`

export const Btn = styled(Link) `
    cursor: pointer;
    border-radius: 10px;
    border: 2px solid #FE4E60;
    text-align: center;
    font-weight: 700;
    padding: 0.5em 1.5em;
    font-family: 'Quicksand', sans-serif;
    letter-spacing: 0.05em;
    transition: 0.2s;
    box-shadow: ${props => props.hover ? "1px 1px 8px 1px #C6DBDE":"0px 0px 8px 0px #FE4E60"};
    background-color: ${props => props.hover ? "#FE4E60":"white"};
    color: ${props => props.hover ? "white":"#FE4E60"};
    &:hover {
        text-decoration: none;
        /* background-color: #FE4E60; */
        background-image: linear-gradient(to bottom right, #798897, #FE4E60);
        color: white;
        box-shadow: 1px 1px 8px 1px #FE4E60;
    }
    @media ${device.mobileS} {
        font-size: 1em;
    }
    @media ${device.tablet} {
        font-size: 1em!important
    }
    @media ${device.laptopL} {
        font-size: 1.5em!important;
    }
    @media ${device.desktop} {
        font-size: 1.75em!important;
    }
`

export const NextBtn = styled(Link) `
    position: fixed;
    right: calc(50vw - 585px);
    margin: 0;
    bottom: 2em;
    padding: 0.5em 1.2em;
    cursor: pointer;
    border-radius: 10px;
    border: 2px solid #B3AA99;
    /* border: 2px solid #FE4E60; */
    text-align: center;
    font-weight: 700;
    padding: 0.5em 1.5em;
    font-family: 'Quicksand', sans-serif;
    letter-spacing: 0.05em;
    transition: 0.2s;
    box-shadow: ${props => props.hover ? "1px 1px 8px 1px #475059":"0px 0px 8px 0px #C3C3C3"};
    background-color: ${props => props.hover ? "#B3AA99":"white"};
    color: ${props => props.hover ? "white":"#B3AA99"};
    &:hover {
        text-decoration: none;
        background-color: #B3AA99;
        /* background-image: linear-gradient(to bottom right, #798897, #FE4E60); */
        color: white;
        box-shadow: 1px 1px 8px 1px #475059;
    }
    @media ${device.mobileS} {
        font-size: 1em;
    }
    @media ${device.tablet} {
        font-size: 1em!important
    }
    @media ${device.laptopL} {
        font-size: 1.2em!important;
    }
    @media ${device.desktop} {
        font-size: 1.55em!important;
    }
`

export const PrevBtn = styled(Link) `
    position: absolute;
    left: calc(50vw - 585px);
    margin: 0;
    bottom: 2em;
    padding: 0.5em 1.2em;
    cursor: pointer;
    text-align: center;
    font-weight: 700;
    font-family: 'Quicksand', sans-serif;
    letter-spacing: 0.05em;
    transition: 0.2s;
    color: ${props => props.hover ? "#333":"#798897"};
    &:hover {
        text-decoration: none;
        color: #333;
    }
    @media ${device.mobileS} {
        font-size: 1em;
    }
    @media ${device.tablet} {
        font-size: 1em!important
    }
    @media ${device.laptopL} {
        font-size: 1.2em!important;
    }
    @media ${device.desktop} {
        font-size: 1.55em!important;
    }
`
