import styled from 'styled-components';
import { device } from './breakpoints.js';
import { Link } from 'react-router-dom';
//The outer container defines the maximum width the page no matter what the screen size is
//The content inside the outer container can either be left-aligned or centered (when the center attribute appears)
export const OuterContainer = styled.div `
    text-align: ${props => props.center ? "center":"left"};
    margin: auto;
    height: ${props => props.full ? "100vh":""};
    @media ${device.mobileS} {
        max-width: 320px;
    }
    @media ${device.tablet} {
        max-width: 720px!important;
    }
    @media ${device.laptop} {
        max-width: 1200px!important;
    }
    @media ${device.laptopL} {
        max-width: 2400px!important;
    }
`

//The inner container defines the maximum width of the content when you want to
//leave more space around your contents
export const InnerContainer = styled.div `
    margin: auto;
    @media ${device.mobileS} {
        width: 320px;
    }
    @media ${device.tablet} {
        width: 80%!important;
    }
    @media ${device.laptop} {
        width: 65%!important;
    }
    @media ${device.laptopL} {
        width: 75%!important;
    }
`

export const OuterFlexBox = styled.div `
    margin: auto 32px auto 32px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
`

//For the cards like the ones on Get Involved page
export const Card = styled(Link) `
    /* box-shadow: 1px 0px 7px 1px #C6DBDE; */
    margin-bottom: 40px;
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;
    box-shadow: 0px 0px 5px 0px #D7D7D7;
    &:hover {
      text-decoration: none;
      /* border: 1px solid #798897; */
      /* box-shadow: 3px 2px 10px 5px #95B0B4; */
      box-shadow: 1px 1px 8px 1px #C3C3C3;
    }
    @media ${device.mobileS} {
        padding: ${props => props.theme.paddingS}!important;
        width: ${props => props.theme.widthS}!important;
    }
    @media ${device.tablet} {
        padding: ${props => props.theme.paddingM}!important;
        width: ${props => props.theme.widthM}!important;
    }
    @media ${device.laptopL} {
        padding: ${props => props.theme.paddingL}!important;
    }
`


export const Card1 = styled(Link) `
    /* box-shadow: 1px 0px 7px 1px #C6DBDE; */
    margin-bottom: 40px;
    display: grid;
    &:hover {
      text-decoration: none;
      /* box-shadow: 3px 2px 10px 5px #95B0B4; */

    }
    @media ${device.mobileS} {
      grid-template-columns: 100%;
    }
    @media ${device.laptop} {
      grid-template-columns: 55% 43%;
      grid-gap: 2%;
    }
    @media ${device.desktop} {
      grid-template-columns: 49% 49%;
      grid-gap: 2%;
    }
`

export const ToolCard = styled.div `
  background: #F3EED9;
`


export const Img = styled.img `
    width: ${props => props.theme.width};
    height: ${props => props.theme.height};
    border-radius: ${props =>props.round ? "5px":""};
    margin: auto;
    &:hover {
      cursor: zoom-in;
    }
    @media ${device.mobileS} {
        height: ${props => props.theme.heightS};
    }
    @media ${device.tablet} {
        height: ${props => props.theme.heightT};
    }
    @media ${device.laptopL} {
        height: ${props => props.theme.heightL};
    }
    @media ${device.desktop} {
        height: ${props => props.theme.height};
    }
`

export const ImgWrapper = styled.div `
  width: 100%;
  /* height: 200px; */
  overflow: hidden;
  /* border: 1px solid #c4c4c4; */
  &:hover {
    box-shadow: 0px 0px 7px 0px #999999;
  }
`

export const Bg = styled.div `
    background-image: ${props => `url(${props.image})`};
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center bottom;
    background-width: 100%;
    @media ${device.mobileS} {
        background-size: contain;
        height: ${props => props.theme.heightS};
    }
    @media ${device.tablet} {
        background-size: cover;
        height: ${props => props.theme.heightM}!important;
    }
    @media ${device.laptop} {
        background-size: cover;
        height: 400px!important;
    }
    @media ${device.laptopL} {
        background-size: cover;
        height: ${props => props.theme.heightL}!important;
    }
`

export const TwoCol = styled.div `
    display: grid;
    @media ${device.mobileS} {
      grid-template-columns: 100%;
    }
    @media ${device.laptopL} {
      grid-template-columns: ${props => props.even ? "49% 49%":"25% 83%"};
      grid-gap: ${props => props.even ? "2%":"2%"};
    }
    @media ${device.desktop} {
      grid-template-columns: ${props => props.even ? "49% 49%":"25% 83%"};
      grid-gap: ${props => props.even ? "2%":"2%"};
    }
`


export const TwoColSpec = styled.div `
    display: grid;
    @media ${device.mobileS} {
      grid-template-columns: 100%;
    }
    @media ${device.laptopL} {
      grid-template-columns: 47% 47%;
      /* align-items: center; */
      grid-gap: 6%;
    }
`


export const ThreeCol = styled.div `
    display: grid;
    @media ${device.mobileS} {
      grid-template-columns: 100%;
    }
    @media ${device.laptopL} {
      grid-template-columns: ${props => props.even ? "30% 30% 30%":"55% 20% 20%"};
      grid-gap: 1%;
    }
`

export const ThreeColTwoRow = styled.div `
    display: grid;
    grid-template-columns: 55% 20% 20%;
    grid-template-rows: auto auto;
    grid-gap: 1%;
`

export const FourColTwoRow = styled.div `
    display: grid;
    grid-template-columns: 25% 25% 25% 25%
    grid-template-rows: auto auto;
    grid-gap: 1%;
`

export const Modal = styled.div `
    width: 100%;
    height: 100%;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.7);
    padding-top: 35px;
    top: 0;
    left: 0;
    z-index: 10;
    display: ${props => props.clicked ? "none":""};
    overflow-y: scroll;
`

export const ModalImg = styled.img `
    margin-left: 15%;
    width: 70%;
    height: auto;
    display: ${props => props.clicked ? "none":""};
`

export const Li = styled.li `
    list-style-type: none;
    font-family: ${props => props.title ? "'EB Garamond', serif":"'Quicksand', sans-serif"};
    color: #A2ACBD;
    line-height: 1.45em;
    font-weight: ${props => props.bold ? "700":""};
    @media ${device.mobileS} {
        font-size: 1em;
    }
    @media ${device.laptopL} {
        font-size: 1.20em;!important;
        line-height: 1.5em!important;
    }
    @media ${device.desktop} {
        font-size: 2em!important;
    }
`

export const Br = styled.br `
    @media ${device.mobileS} {
      display: none;
    }
    @media ${device.tablet} {
      display: none;
    }
    @media ${device.laptop} {
      display: block;
    }
`

export const Intro = styled.div `
    margin: 240px auto 180px auto;
`
