import React, { Component } from 'react';
import { H1, H2, H4, H5, Span } from '../css/fonts.js';
import { OuterContainer, InnerContainer, Img, TwoCol, Br } from '../css/container.js';
import { meContent } from '../content.js';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';

class AboutMe extends Component {
  handleClick = e => {
    const target = document.getElementById(e.target.title);
    target.scrollIntoView();
  }
  render() {
    return (
      <div>
        <Navbar/>
          <InnerContainer>
            <br/><br/><br/><br/>
            <Br/><Br/>
            <H1 large center>{meContent.title}</H1><br/>
            <H5 dangerouslySetInnerHTML={{ __html: meContent.journey }} />
            <img src={meContent.journeyimg} alt={meContent.journeyimg} style={{"width":"100%"}}/>

            <br/><br/><br/><br/>
            <H2>My Journey into UX</H2>
            <TwoCol even>
              <div>              
              <H5 dangerouslySetInnerHTML={{ __html: meContent.passion1 }} />
              <H5 dangerouslySetInnerHTML={{ __html: meContent.passion2 }} />
              <H5 dangerouslySetInnerHTML={{ __html: meContent.passion3 }} />
              </div>
              <Img border src={meContent.img} alt={meContent.img} theme={{width:"100%"}}/>
              </TwoCol>
              <br/><br/>
              <H2>Skills</H2>
              <TwoCol even>
                <H5 dangerouslySetInnerHTML={{ __html: meContent.skill1 }} />
                <H5 dangerouslySetInnerHTML={{ __html: meContent.skill2 }} />
              </TwoCol>
            <br/><br/>

              {/**<H2>I love ...</H2>
              <div>
                <H5>Neuroscience</H5>
                <H5>Cooking</H5>
              </div>
              <H2>I like ...</H2>
              <H5>Neuroscience</H5>**/}



          </InnerContainer>
        <br/><br/><br/><br/>
        <Footer/>
      </div>
    );
  }
}

export default AboutMe;
