import React, { Component } from 'react';
import { H1, H2, H5, H5U, P, Subtitle, Transition, NextBtn, Highlights } from '../css/fonts.js';
import { OuterContainer, InnerContainer, Br, TwoCol, TwoColSpec } from '../css/container.js';
import { srContent } from '../content.js';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import Image from '../components/Image';
import Tools from '../components/Tools';

class ScatteringResources extends Component {
  render() {
    return (
      <div>
        <Navbar/>
        <InnerContainer>
          <br/><br/><br/><br/>
          <br/><Br/><Br/>
          <H1 large center>{srContent.title}</H1>
          <br/>
          {/**<img src={srContent.header} alt={srContent.header} style={{"width":"100%"}}/>**/}
          <br/><Br/>
          <TwoCol>
            <P right dangerouslySetInnerHTML={{ __html: srContent.other }}/>
            <H5 dangerouslySetInnerHTML={{ __html: srContent.role }}/>
          </TwoCol>
          <br/><br/>
          <Subtitle>CONTEXT</Subtitle>
          <H5 dangerouslySetInnerHTML={{ __html: srContent.context }} />
          <br/>
          <Subtitle>CLIENT REQUEST</Subtitle>
          <H5 dangerouslySetInnerHTML={{ __html: srContent.request }} />
          <Highlights dangerouslySetInnerHTML={{ __html: srContent.quote1 }} />
          <br/>
          <Subtitle>PROJECT TIMELINE</Subtitle><br/>
          <img src={srContent.planimg} alt={srContent.planimg} style={{"width":"100%"}}/><br/>
          <br/><br/><br/>
          <Subtitle>UX AUDIT</Subtitle>
          <div>
            <H5 dangerouslySetInnerHTML={{ __html: srContent.audit }} />
            <br/>
            <img src={srContent.uaimg} alt={srContent.uaimg} style={{"width":"100%"}}/><br/>
          </div>
          <br/><br/><br/>
          <Subtitle>PRIORITIZE NEEDS</Subtitle>
          <H5 dangerouslySetInnerHTML={{ __html: srContent.needs }} />
          <br/><br/>
          <Subtitle>USER INTERVIEW</Subtitle>
          <H5 dangerouslySetInnerHTML={{ __html: srContent.interview }} />
          <Highlights dangerouslySetInnerHTML={{ __html: srContent.quote2 }} />
          <img src={srContent.interviewimg} alt={srContent.interviewimg} style={{"width":"100%"}}/>
          <br/><br/><br/>
          <Subtitle>CHALLENGE</Subtitle>
          <H5 dangerouslySetInnerHTML={{ __html: srContent.challenge }} />
          <br/><br/>
          <Subtitle>WIREFRAMES</Subtitle>
          <H2>Redesigning the Get Involved and Donation page</H2>
          <H5 dangerouslySetInnerHTML={{ __html: srContent.redesign }}/><br/>
          <img src={srContent.redesignimg} alt={srContent.redesignimg} style={{"width":"100%"}}/>

          <br/><br/><br/>
          <Subtitle>USABILITY TESTS</Subtitle>
          <H5 dangerouslySetInnerHTML={{ __html: srContent.ut }} />
          <H5 dangerouslySetInnerHTML={{ __html: srContent.process }} />
          <Highlights dangerouslySetInnerHTML={{ __html: srContent.quote4 }} />
          <br/>
          <TwoColSpec>
            <img src={srContent.utimg} alt={srContent.utimg} style={{"width":"100%"}}/>
            <img src={srContent.utimg1} alt={srContent.utimg1} style={{"width":"100%"}}/>
          </TwoColSpec>
          <br/><br/>

          <img src={srContent.utdataimg} alt={srContent.utdataimg} style={{"width":"100%"}}/>
            <br/><br/><br/>
            <Subtitle>LEARNINGS</Subtitle>
            <H5 dangerouslySetInnerHTML={{ __html: srContent.learning }} />
            <br/><br/>

            <Subtitle>Next steps</Subtitle>
            <H5 dangerouslySetInnerHTML={{ __html: srContent.nextstep }} />
        </InnerContainer>
        <NextBtn to={srContent.link}>
          <div dangerouslySetInnerHTML={{ __html: srContent.action }} />
        </NextBtn>
        <br/><br/><br/><br/>
        <Footer/>
      </div>
    );
  }
}

export default ScatteringResources;
