import React, { Component } from 'react';
import { H1, H3, H4, H5, H6, Span, Btn } from '../css/fonts.js';
import { OuterContainer, InnerContainer, OuterFlexBox, Card, Intro } from '../css/container.js';
import { projectSummary } from '../content.js';
import Navbar from '../components/Navbar.js';
import Footer from '../components/Footer';
import './style.css';

class Home extends Component {
  constructor() {
    super();
    this.state={
      hover: ""
    }
  }

  hover = e => {
    const target = e.currentTarget.id;
    if (this.state.hover === target) {
      this.setState({hover: ""});
    }
    else {
      this.setState({hover: target});
    }
  }


  handleClick = e => {
    const target = document.getElementById("takeaways");
    console.log("target");
    console.log(e.target.title);
    target.scrollIntoView();
  }

  render() {
    return (
      <div>
        <Navbar/>
        <OuterContainer>
          <InnerContainer>
            <Intro>
              <H1 bold>Hi! I'm Yuhan (Hannah) Wang.</H1><br/>
              <H4>I am an aspiring UX researcher, with experience also in UX design and front-end development.</H4>
                  {/* I want to design systems that could enhance people
                  */}
                {/**I like to think out of the box, and imagine all other ways our world could possibly be.**/}
              <H4>I like to trace back to the cause, understand why things are, how they evolve, and how they change in today’s intricately interconnected global system.</H4>
            </Intro>
          </InnerContainer>
        </OuterContainer>
            {/**************************Projects*************************/}
            <OuterFlexBox>
              {projectSummary.map(project => {
                const id = project.id;
                return (
                <Card
                  key={project.id}
                  onMouseEnter={this.hover}
                  onMouseLeave={this.hover}
                  id={project.id}
                  theme={{widthM:"47%", widthS: "100%"}}
                  to={project.link}
                >
                  <div>
                    <img src={project.img} alt={project.img} style={{"width": "100%"}}/>
                    <div style={{"padding":"48px 30px 20px 30px"}}>
                      <H1>{project.title}</H1>
                      <H6 light dangerouslySetInnerHTML={{ __html: project.subtitle }}/>
                      <br/>
                      <H5 dangerouslySetInnerHTML={{ __html: project.summary }} />
                      <br/>
                      {/**<Btn to={project.link} hover={this.state.hover === id}>
                        Learn More
                      </Btn>**/}
                    </div>
                  </div>
                  <br/>
                </Card>
              )})}
          </OuterFlexBox>
        <br/><br/>
        <Footer/>
      </div>
    );
  }
}

export default Home;
